.container-table {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: stretch;
  font-size: small;

  thead th {
    background-color: var(--primaryColor);
  }
  th,
  td {
    text-align: center;
    background-color: rgba(107, 92, 92, 0.2);
    color: #785555;
    padding: 10px 0 10px;
  }

  th {
    text-align: center;
    color: #000;
    padding: 8px 0 8px;
  }
  tr:hover {
    background-color: rgba(224, 24, 24, 0.3);
  }

  @media only screen and (min-width: 375px) {
    th,
    td {
      padding: 12px 0 12px;
    }
    th {
      font-size: 14px;
    }
  }
  @media only screen and (min-width: 768px) {
    th,
    td {
      padding: 10px 0 10px;
      font-size: 20px;
    }
    th {
      font-size: 22px;
    }
  }
  .h-2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
    .border {
      border: 1px solid var(--primaryColor);
      border-radius: 6px;
      padding: 6px;
      margin-right: 10px;
      &:hover,
      &:focus {
        border: 1px solid var(--onFocus);
      }
    }
    .b-1 {
      text-align: center;
    }
    .h-3 {
      margin-top: 10px;
      margin-bottom: 10px;
    }
    .h-4 {
      padding-bottom: 10px;
      .i-last {
        border: 1px solid var(--primaryColor);
        border-radius: 6px;
        &:focus,&:hover{
          border: 1px solid var(--onFocus);
        }
      }
    }
  }
}
